
body {

    background-color: #95c2de;
}

.mainbox {
    max-width: 100vw;
    color: #05445E;
    background-color: #95c2de;
    margin: auto;
    height: 100vh;
    position: relative;
}

.err {
    color: #ffffff;
    font-size: 11rem;
    position: absolute;
    left: 20%;
    top: 8%;
}

.far {
    border-radius: 100%;
    position: absolute;
    font-size: 8.5rem;
    left: 42%;
    top: 15%;
    color: #ffffff;
}

.err2 {
    color: #ffffff;
    font-family: 'Nunito Sans', sans-serif;
    font-size: 11rem;
    position: absolute;
    left: 68%;
    top: 8%;
}

.msg {
    text-align: center;
    font-family: 'Nunito Sans', sans-serif;
    font-size: 1.6rem;
    position: absolute;
    left: 16%;
    top: 45%;
    width: 75%;
}

a {
    text-decoration: none;
    color: #D4F1F4;
}

a:hover {
    text-decoration: underline;
}
